<template>
  <section class="login">
	<canvas></canvas>
    <div class="form " id='iphone_login' @keyup.enter="doLogin">
      <p class="icon">
        <i class="fa fa-fire fa-2x"></i>
      </p>
      <p class="input">
        <i class="fa fa-user fa-fw"></i>
        <input id="userName"
               type="text"
               name="userName"
               placeholder="用户名"
               v-model.trim="name"
        >
      </p>
      <p class="input">
        <i class="fa fa-key fa-fw"></i>
        <input id="password"
               type="password"
               placeholder="密码"
               v-model.trim="pwd"
        >
      </p>
      <p class="info"  >Test Account:wang  Secret:123456</p>
      <p class="info">{{info}}</p>
      <p>
        <button @click="doLogin()">登陆
        </button>
      </p>
      <p>
        <button @click="sign_up">sign_up
        </button>
      </p>
    </div>
  </section>
</template>
<script>
  //import {mapActions} from 'vuex'
  //import {mapState, mapActions}   from 'vuex'
  import {mapState, mapActions, mapMutations}   from 'vuex'
  import {set}  from '../../assets/js/cookieUtil'

  export default{
    data(){
      return {
        name: '',
        pwd: '',
        info: ''
      }
    },
    mounted(){
		if(browser.versions.mobile){
			document.addEventListener('touchmove', function (e) {
				e.preventDefault()
			})
			var c = document.getElementsByTagName('canvas')[0],
				x = c.getContext('2d'),
				pr = window.devicePixelRatio || 1,
				w = window.innerWidth,
				h = window.innerHeight,
				f = 90,
				q,
				m = Math,
				r = 0,
				u = m.PI*2,
				v = m.cos,
				z = m.random
			c.width = w*pr
			c.height = h*pr
			x.scale(pr, pr)
			x.globalAlpha = 0.6
			function i(){
				x.clearRect(0,0,w,h)
				q=[{x:0,y:h*.7+f},{x:0,y:h*.7-f}]
				while(q[1].x<w+f) d(q[0], q[1])
			}
			function d(i,j){
				x.beginPath()
				x.moveTo(i.x, i.y)
				x.lineTo(j.x, j.y)
				var k = j.x + (z()*2-0.25)*f,
					n = y(j.y)
				x.lineTo(k, n)
				x.closePath()
				r-=u/-50
				x.fillStyle = '#'+(v(r)*127+128<<16 | v(r+u/3)*127+128<<8 | v(r+u/3*2)*127+128).toString(16)
				x.fill()
				q[0] = q[1]
				q[1] = {x:k,y:n}
			}
			function y(p){
				var t = p + (z()*2-1.1)*f
				return (t>h||t<0) ? y(p) : t
			}
			document.onclick = i
			document.ontouchstart = i
			i()
		}
		else {

      this.$store.state.showCanvas=true;
    }
    },
    methods: {
      doLogin(){
        if (!this.name.length) return this.info = '请输入正常的用户名'
        if (!this.pwd.length) return this.info = '请输入正常的密码'

        this.login({name: this.name, pwd: this.pwd})
          .then(() => {
            const date = new Date(Date.now() + 60000 * 30)
            set('user', this.name, date, '/', window.location.hostname)
            this.$router.push({path: '/console'})
          })
          .catch(msg => this.info = msg)
      },
      sign_up(){
		this.$router.push({path: '/sign_up'})
      },
      clearInfo(){
        this.info = ''
      },
      ...mapActions(['login'])
    },
    watch: {
      name: 'clearInfo',
      pwd: 'clearInfo'
    }
  }

let browser={
    versions:function(){
            var u = navigator.userAgent, app = navigator.appVersion;
            return {         //移动终端浏览器版本信息
                 trident: u.indexOf('Trident') > -1, //IE内核
                presto: u.indexOf('Presto') > -1, //opera内核
                webKit: u.indexOf('AppleWebKit') > -1, //苹果、谷歌内核
                gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //火狐内核
                mobile: !!u.match(/AppleWebKit.*Mobile.*/), //是否为移动终端
                ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios终端
                android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //android终端或uc浏览器
                iPhone: u.indexOf('iPhone') > -1 , //是否为iPhone或者QQHD浏览器
                iPad: u.indexOf('iPad') > -1, //是否iPad
                webApp: u.indexOf('Safari') == -1 //是否web应该程序，没有头部与底部
            };
         }(),
         language:(navigator.browserLanguage || navigator.language).toLowerCase()
}
let dynamicLoading = {
  css: function(path){
 if(!path || path.length === 0){
  throw new Error('argument "path" is required !');
 }
 var head = document.getElementsByTagName('head')[0];
    var link = document.createElement('link');
    link.href = path;
    link.rel = 'stylesheet';
    link.type = 'text/css';
    head.appendChild(link);
  },
  js: function(path){
 if(!path || path.length === 0){
  throw new Error('argument "path" is required !');
 }
 var head = document.getElementsByTagName('head')[0];
    var script = document.createElement('script');
    script.src = path;
    script.type = 'text/javascript';
    head.appendChild(script);
  }
}
if(browser.versions.android){
	//document.querySelector('script').sheet.insertRule('#iphone_login {color:red;}', 1)

	//let tt=getElementsByClassName('test')
	//tt.setAttribute('id','iphone')
	dynamicLoading.css("test.css");
}

</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../style/variables";
  section {
    position:absolute;
    //left:0%;
    //top:20%;
	width:100%;
	height:100%;
	//left:0%;
	//top:10%;
  }
 section.login {
    .form {

      width:100%;
      position:absolute;
      left:50%;
      top:50%;
      //margin:0 50px;

      transform: translate(-50%, -50%);
      .icon i {
        transition: all 4s;
        &:hover {
          transform: rotate(1440deg);
        }
      }
      > p {
        //height: 50px;
        text-align: center;
        transition: all 0.4s;
        &.info {
          //margin: 10px;
          font-size: 12px;
          //height: 20px;
          color: $black3;
        }
        &.input:hover {
          color: $green2;
        }
        input {
          transition: all 0.4s;
          //width: 200px;
          //width:40%;
          //padding: 8px;
          border-bottom: 1px solid $green1;
          &:focus {
            //width: 210px;
            //width:45%;
          }
        }
        button {
          transition: all 0.4s;
          color: $black3;
          border: 1px solid $green1;
          border-radius: 4px;
          width: 60px;
          height: 30px;
          &:hover {
            background-color: $green1;
            color: #FFF;
          }
        }
      }
    }
    @media screen and (max-width: 500px) {
		.form {
			position:absolute;
			left:0%;
			top:25%;
			//margin:50% 50%;
		}
    }
    }

</style>



